

import Vue from 'vue'
import router from './router'





import './assets/style/currency.css'
import './assets/style/animate.min.css'

import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad,{




throttleWait:200
})

import http from './api/http'
import ports from './api/ports'
Vue.prototype.http = http
Vue.prototype.ports = ports
import axios from 'axios'
Vue.config.debug = true

Vue.config.productionTip = false
Vue.prototype.$axios = axios
axios.defaults.withCredentials=true;


axios.interceptors.request.use(
config => {
if (localStorage.getItem('openId')) {
config.headers.openid = localStorage.getItem('openId');
}
if(localStorage.parentmid){
config.headers.parentmid = localStorage.getItem('parentmid');
}
return config;
},
error => {
return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {

if (response.data.code===30005){
localStorage.removeItem('openId')

router.push({
path:'/author/index',
})
}else if(response.data.code===30006){

router.replace({
path:'/author/code',
})
}else{
return response
}
}, function (error) {

return Promise.reject(error)
})

import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper, /* { default global options } */)

import store from './store/index'

import {
Style,

Button,
Loading,
Tip,
Toolbar,
TabBar,
TabPanels,

Checkbox,
CheckboxGroup,
Checker,
Radio,
RadioGroup,
Input,
Textarea,
Select,
Switch,
Rate,
Validator,
Upload,
Form,

Popup,
Toast,
Picker,
CascadePicker,
DatePicker,
TimePicker,
SegmentPicker,
Dialog,
ActionSheet,
Drawer,
ImagePreview,

Scroll,
Slide,
IndexList,
Swipe,
Sticky,
ScrollNav,
ScrollNavBar,
RecycleList
} from 'cube-ui'
import App from './App'

import myLoading from './components/loading.vue'  
Vue.component("my-loading",myLoading); 
Vue.use(Button)
Vue.use(Loading)
Vue.use(Tip)
Vue.use(Toolbar)
Vue.use(TabBar)
Vue.use(TabPanels)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(Checker)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(Input)
Vue.use(Textarea)
Vue.use(Select)
Vue.use(Switch)
Vue.use(Rate)
Vue.use(Validator)
Vue.use(Upload)
Vue.use(Form)
Vue.use(Popup)
Vue.use(Toast)
Vue.use(Picker)
Vue.use(CascadePicker)
Vue.use(DatePicker)
Vue.use(TimePicker)
Vue.use(SegmentPicker)
Vue.use(Dialog)
Vue.use(ActionSheet)
Vue.use(Drawer)
Vue.use(ImagePreview)
Vue.use(Scroll)
Vue.use(Slide)
Vue.use(IndexList)
Vue.use(Swipe)
Vue.use(Sticky)
Vue.use(ScrollNav)
Vue.use(ScrollNavBar)
Vue.use(RecycleList)


import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(LoadingPlugin)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



