  import './../assets/style/cubeRest.css'
  export default {
    data () {
      return {
        selectedLabelSlots: '首页',
        tabs: [{
          label: '首页',
          icon: 'cubeic-home'
        }, {
          label: '分类',
          icon: 'cubeic-like'
        }, {
          label: '购物车',
          icon: 'cubeic-vip'
        }, {
          label: '我的',
          icon: 'cubeic-person'
        }],
        showSlider: false,
        inline: true,
        useTransition: false
      }
    },
    props:{
      selectedNavTitle: String,
    },
    methods: {
      clickHandler (label) {
        // if you clicked home tab, then print 'Home'
//        this.$store.commit('changeTitle',label)
        switch (label){
          case '首页':
            this.$router.push('/')
            break
          case '我的':
            this.$router.push('/me/index')
            break
          case '分类':
            this.$router.push('/sort/index')
            break
          case '购物车':
            this.$router.push('/cart/index')
            break
          default:
            this.$router.push('/')
        }

      },
      changeHandler (label) {
        // if you clicked different tab, this methods can be emitted
//        console.log(this.$route.path)
      }
    },
    mounted (){
//      this.clickHandler(this.selectedNavTitle)
    },
  }



